          <h1>江西师范大学计算机信息工程学院简介</h1>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;计算机信息工程学院始创于1985年5月的江西师范大学数学系计算机教研组。三十五年来，学院秉承“诚实做人，踏实做事”的院训，围绕立德树人的根本任务，坚持以教学为中心，坚持质量立院，人才兴院，特色强院，以“厚基础、重技能、强素质”为培养目标，着力培养高素质双师型教师和创新型计算机工程应用人才，力争把学院办成江西省前列、国内有特色和影响力的教学研究型学院。</p>
          <h3>一、历史沿革</h3>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;1985.05—1994.12，江西师范大学计算机科学系</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;995.01—1996.06，江西师范大学数学与计算机学院</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;1996.07—2003.07，江西师范大学计算机科学技术学院</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;2003.08—至今 ，江西师范大学计算机信息工程学院</p>
          <h3>二、基本情况</h3>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;学院现有教职工104人，本科生1137人，统招硕士研究生108人，海外留学生78人。</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;现有4个本科专业， 2个一级学科硕士学位授权点，
            2个工程硕士专业学位领域，1个教育硕士学科教学方向，1个目录外博士培养方向，拥有6个省部级科研平台。</p>
          <h3>三、师资队伍</h3>
          <p>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;学院有一支师德高尚、业务过硬、团结奋进、勇于创新、结构合理的师资队伍。目前在编在岗教职工总人数104人，其中：专职教师85人，教授16人、副教授42人，博士生导师3人，博士33人，全国模范教师2人、省级教学名师2人、江西省模范教师1人、省高校中青年学科带头人4人、江西省五一劳动奖章获得者1人、江西省师德标兵1人、江西省双千计划2人。</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;学院现拥有1个省级教学团队，6门省级精品和精品资源共享课程，近年来，出版了8部国家"十一五"、"十二五"国家级规划教材和省部级优秀教材，获得江西省教学成果一等奖3项、二等奖2项。</p>
          <h3>四、人才培养</h3>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;现有计算机科学与技术（兼师范）、物联网工程、数据科学与大数据技术，人工智能4个本科专业。
            “计算机科学与技术”是江西省本科品牌专业，并入选江西省卓越工程师建设计划和专业综合改革项目。计算机科学与技术专业于2016年在江西省本科专业综合评价工作中荣获全省第一名，于2019年获批江西省一流本科专业，并于同年获批国家级一流本科专业建设点。</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;加强本科生教育。学院始终坚持以学生为本，以育人为宗旨，高度重视学生思想品德、专业知识技能、创新能力等方面的培养。近五年来，学生在中国机器人大赛、中国智能机器人大赛、全国数学建模大赛等竞赛中获得奖励200多项，成功申报学校实验开放项目、创新创业课题100余项，完成和维护校内外各类网站60余个，完成各种项目实训70余项。</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注重研究生教育。获江西省高等学校研究生教学成果二等奖1项,江西省研究生优质课程 2
            门；着重加强研究生求实精神、合作精神和创新能力的培养，构建了江西省“计算机科学与技术”学科研究生创新基地，与省内外10多个企业建立了研究生产学研合作基地；近五年来，学生参加国家和江西省数学建模比赛获奖40余项，参加江西省首届专业学位研究生职业能力竞赛获二等奖2项，优秀奖1项。研究生培养质量不断提高，就业渠道不断拓宽，初次就业率均为100%。</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;开展国际化办学。自2016年开始招收计算机科学与技术专业来华留学生。目前已连续招生四届，共有本科留学生95人，研究生留学生5人。在各类中外文化交流竞赛和展示中表现出色，取得良好成绩。</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在几代计算机人的共同努力下，三十五年来，学院已培养了各类学生6000余人，为国家和社会输送了一大批品德优良、基础扎实、技能过硬、勇于创新的计算机专门人才和双师型计算机教育人才。毕业生以基础知识扎实、工作敬业求实而深受用人单位的好评。</p>
          <h3>五、学科建设与科学研究</h3>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;学院现有计算机科学与技术和软件工程2个一级学科硕士学位授权点，电子信息类（软件工程和计算机技术）工程硕士专业学位领域，1个教育硕士学科教学方向（现代教育技术），1个目录外博士培养方向（中文信息处理）。“计算机科学与技术”是江西省重点学科。2016年，学院成为江西省高校计算机科学与技术学科联盟牵头单位。</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;学院已建成江西省高等学校物联网与智能计算高水平工程研究中心、江西省高性能计算技术重点实验室、江西省分布式计算工程技术研究中心、江西省“计算机科学与技术”学科研究生创新基地和网络化支撑软件国家级国际科技合作基地、江西省高校软件科学技术中心。参与建设江西省高等学校"2011"协同创新中心："基于大数据的江西省教师质量监测、评估与服务"，形成了以省级重点学科为依托，具有鲜明特色和优势的学科建设体系与研究平台。</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;近五年来，学院共承担了四十多项国家级科研项目，获得省部级科研奖励十余项。</p>
          <h3>六、教学基础设施</h3>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;学院教学设施完备。学院计算机实验教学中心是江西高校实验教学示范中心，现有软件综合实验室、硬件实验室、网络工程与安全实验室、嵌入式实验室和物联网实验室，面积达3000平方米。学院为学生开设了自主管理的创新实验室和课外活动室。不断优化的办学条件为学院的稳步快速发展和人才培养质量的不断提高，提供了坚实的物质基础与保障。（以上数据截至2020年12月）</p>
